.header_mobile {
	position: relative;

	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		padding: 1.6rem var(--gl-indent);

		background-color: $white;

		// transition: margin 0.3s ease;

		// .body--open_menu_state & {
		// 	margin-right: 2rem;
		// }
		&:not(.header--scroll_state &) {
			border-bottom: 1px solid $beige_2;
		}

		&::after {
			content: "";
			position: absolute;
			top: 0;
			right: 0;
			width: 100%;
			height: 100%;

			z-index: 4;

			transform: translateX(100%);
			background-color: $white;

			transition: transform 0.3s ease;

			.body--open_menu_state & {
				transform: translateX(0);
			}
		}
	}
}

.header_mobile__in {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: stretch;

	height: 100%;

	padding: 2.4rem 3.2rem;
	background-color: $white;

	@include bp($point_2, min) {
		height: 100%;
	}
}

.header_mobile__wrap {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	align-content: center;
}

.header_mobile__col {
	&:nth-child(3n + 1),
	&:nth-child(3n + 3) {
		width: 25%;
	}

	&:nth-child(3n + 2) {
		width: 50%;
		padding: 0 1rem;
	}
}

.header_mobile__logo {
	flex-shrink: 0;
	display: block;

	max-width: 14.8rem;
	margin: 0 auto;
	height: 3.2rem;

	color: $black;
}

.header_mobile__logo_img {
	@include contain_img;
}

.header_mobile__body__search {
	&:not(:last-child) {
		margin-bottom: 1.4rem;
	}
}

.header_mobile__search_wrap {
	position: absolute;
	top: 100%;
	left: 0;
	width: 100%;
	height: auto;

	padding: 1.4rem var(--gl-indent);

	background-color: $beige_1;

	transform: translateY(-100%);
	opacity: 0;
	visibility: hidden;

	transition: opacity 0.3s ease, transform 0.3s ease;

	&.header_mobile__search_wrap--state {
		transform: translateY(0);
		opacity: 1;
		visibility: visible;
	}
}

.header_mobile__search_wrap {
	margin-left: auto;
}

